var province = $("#province"), city = $("#city"), town = $("#town");

if (province.val() == '') {
    for (var i = 0; i < provinceList.length; i++) {
        addEle(province, provinceList[i].name);
    }
} else {
    let ps = province.val()
    removeEle(province)
    for (var i = 0; i < provinceList.length; i++) {
        if (ps == provinceList[i].name) {
            let optionStr = "<option selected value=" + ps + ">" + ps + "</option>";
            province.append(optionStr);
        } else {
            addEle(province, provinceList[i].name);
        }
    }
}

if (province.val() != '') {
    let cityItem = cityIndex();
    $.each(provinceList[cityItem].cityList, function (i, item) {
        if (city.val() == item.name) {
            let cs = city.val()
            removeEle(city)
            let optionStr = "<option selected value=" + cs + ">" + cs + "</option>";
            city.append(optionStr);
        } else {
            addEle(city, item.name)
        }
    })
}
if (city.val() != '') {
    let cityText = city.val();
    let ts = town.val();
    let cityItem = cityIndex();
    $.each(provinceList[cityItem].cityList, function (i, item) {
        if (cityText == item.name) {
            removeEle(town)
            for (var n = 0; n < item.areaList.length; n++) {
                if (ts == item.areaList[n]) {
                    let optionStr = "<option selected value=" + ts + ">" + ts + "</option>";
                    town.append(optionStr);
                } else {
                    addEle(town, item.areaList[n])
                }
            }
        }
    });
}

province.on("change", function () {
    let cityItem = cityIndex();
    removeEle(city);
    removeEle(town);
    if(province.val()!=''){
        $.each(provinceList[cityItem].cityList, function (i, item) {
            addEle(city, item.name)
        })
    }
});
city.on("change", function () {
    let cityText = $(this).val();
    removeEle(town);
    let cityItem = cityIndex();
    if(city.val()!=''){
        $.each(provinceList[cityItem].cityList, function (i, item) {
            if (cityText == item.name) {
                for (var n = 0; n < item.areaList.length; n++) {
                    addEle(town, item.areaList[n])
                }
            }
        });
    }
});

function addEle(ele, value) {
    var optionStr = "";
    optionStr = "<option value=" + value + ">" + value + "</option>";
    ele.append(optionStr);
}

function removeEle(ele) {
    ele.find("option").remove();
    var optionStar = "<option value=" + "0" + ">" + "" + "</option>";
    ele.append(optionStar);
}

function cityIndex() {
    let cityItem = ''
    $.each(provinceList, function (i, item) {
        if (province.val() == item.name) {
            cityItem = i;
            // return cityItem
        }
        return cityItem
    });
    return cityItem
}


